import React from 'react';
import * as emailjs from 'emailjs-com'

export default class extends React.Component {
    constructor(props) {
        super(props);
        this.state = { feedback: '', subject: '', email: '' };
        this.handleChange = this.handleChange.bind(this);
        this.handleSenderChange = this.handleSenderChange.bind(this);
        this.handleEmailChange = this.handleEmailChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    handleSubmit(event) {
        event.preventDefault();
        let variables = {
            message_html: this.state.feedback,
            subject: this.state.subject,
            from_name: this.state.email,
            to_name: 'saifedine.hajji@gmail.com',
        };
        emailjs.send('gmail', 'template_fmJdzRkd', variables, 'user_OnGGh4USn5qrA9vCvPA4Y');
    }

    render() {
        return (


            <section id="contact">

                <div className="" style={{ textAlign: 'center' }} >
                    <h2 style={{ fontFamily: 'Liu Jian Mao Cao', letterSpacing: '2px', fontSize: '50px', color: '#11abb0', paddingTop: '1em' }}
                    > LET'S GET IN TOUCH !</h2>

                    <hr style={{ width: '50%', marginLeft: '25%', }} />


                    <form class="form-style-9" onSubmit={this.handleSubmit}>
                        <ul>
                            <li>
                                <input type="text" name="field1" class="field-style field-split align-center" placeholder="Subject"
                                    onChange={this.handleSenderChange}
                                    required
                                    value={this.state.subject}
                                />
                                <input type="email" name="field2" class="field-style field-split align-center" placeholder="Email"
                                    onChange={this.handleEmailChange}
                                    required
                                    value={this.state.email}
                                />

                            </li>


                            <li>
                                <textarea name="field5" class="field-style"

                                    onChange={this.handleChange}
                                    placeholder="Add your message Here..."
                                    required
                                    value={this.state.feedback}
                                ></textarea>
                            </li>
                            <li>
                                <input type="submit" value="Send Me" />
                            </li>
                        </ul>
                    </form>
                </div>
            </section>
        )
    }

    handleChange(event) {
        this.setState({ feedback: event.target.value })
    }
    handleSenderChange(event) {
        this.setState({ subject: event.target.value })
    }
    handleEmailChange(event) {
        this.setState({ email: event.target.value })
    }




}